@import '../../globalScss//globalScss.scss';

.navbar-continer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000;
    background-color: white;
    box-shadow: 0px 5px 0px -2px rgba(72, 50, 72, 0.5);
  
    .navbar-links-div {
      display: flex;
      justify-content: space-between;
      align-items: center;
  
      .link {
        list-style: none;
        text-decoration: none;
        color: rgb(0, 0, 0);
        margin-left: 20px;
        transition: all 0.5s ease-in-out;
        cursor: pointer;
        font-weight: 500;
  
        &:hover {
          margin-left: 13px;  
          font-size: 18px;
          color: $mainColor;
          font-weight: 600;
        }
      }
    }
  }
  
